<template>
    <!---->
    <div>
        <el-table :data="planList" border :header-cell-style="$root.tableHeaderStyle()" :cell-style="'text-align:center'">
            <el-table-column prop="name" label="" width="150" />
            <el-table-column prop="planType" label="" width="150" >
                <template #default="scope">
                    <span v-show="scope.row.planType === 0">
                            <el-tag></el-tag>
                    </span>
                    <span v-show="scope.row.planType === 1">
                            <el-tag>Web</el-tag>
                    </span>
                    <span v-show="scope.row.planType === 2">
                            <el-tag>App</el-tag>
                    </span>
                </template>
            </el-table-column>
            <el-table-column prop="status" label="" width="100" >
                <template #default="scope">
                   <el-tag v-if="scope.row.status === '0'" type="danger"></el-tag>
                   <el-tag v-if="scope.row.status === '1'" type="success"></el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="clock" label="Clock" width="150"/>
            <el-table-column prop="remark" label=""/>
            <el-table-column label="" >
                <template #default="scope">
                    <slot name="runCase" :data="scope.row">
                        <el-button size="small" type="primary" @click="runAlter = true"> </el-button>
                        <!---->
                        <el-dialog v-model="runAlter" title="" width="20%" center>
                            <!-- mainTitle-->
                            <slot name="title">
                                <span>?</span>
                            </slot>
                            <template #footer>
                          <span class="dialog-footer">
                            <el-button @click="runAlter = false"></el-button>
                            <el-button type="primary" @click="runPlan(scope.row)"></el-button>
                          </span>
                            </template>
                        </el-dialog>
                    </slot>
                    <el-button style="margin-left: 5px" v-if="scope.row.status ==='0' " size="small" type="danger" @click="suspended(scope.row,1)"><el-icon :size="13"><VideoPause/></el-icon></el-button>
                    <el-button  style="margin-left: 5px" v-if="scope.row.status ==='1' " size="small" type="success" @click="suspended(scope.row,0)"><el-icon :size="13"><VideoPlay/></el-icon></el-button>
                    <slot name="editCase" :data="scope.row">
                        <el-button size="small" type="info" @click="updatePlan(scope.row)"> </el-button>
                    </slot>
                    <el-button size="small" type="danger" @click="delPlan(scope.row.id)"> </el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import request from "../../utils/request";
    import {VideoPlay,VideoPause} from "@element-plus/icons"

    export default {
        name: "planList",
        components:{
            VideoPlay,VideoPause
        },
        props:{
            planList:{
                default : []
            }
        },
        data(){
            return{
                runAlter:false
            }
        },
        methods:{
            delPlan(planId){
                //
                request.delete("/api/plan/delete/"+planId).then(res=>{
                    this.$root.toast(res)
                    this.$parent.load()
                })

            },
            // 
            updatePlan(data){
                this.$router.push({
                    path:"/case/newPlan",
                    query:{
                        plan:JSON.stringify(data)
                    }
                })
            },
            // 
            suspended(data,type){
                if(type === 0){
                    data.status = "0"
                }else {
                    data.status = "1"
                }
                request.post("/api/plan/save",data)
            },
            //
            runPlan(planInfo){
                request.post("/api/plan/runPlan",planInfo).then(res=>{
                })
                this.runAlter = false
                this.$root.my_toast()
                this.$router.push("/case/result")
            }
        }
    }
</script>

<style scoped>
    .bottom{

    }
</style>