import {ElMessage, ElMessageBox} from 'element-plus'

// 
function deepClone(obj, newObj) {
    newObj = newObj || {};
    for (let key in obj) {
        if (typeof obj[key] == 'object') {
            let isArray = Array.isArray(obj[key]);//
            newObj[key] = (isArray === true ? [] : {})
            deepClone(obj[key], newObj[key]);
        } else {
            newObj[key] = obj[key]
        }
    }
    return newObj;
}

// toast 
function toast(res){
    try {
        if (res.code === "0"){
            ElMessage({
                type: 'success',
                message: res.msg,
            })
        }else {
            ElMessage({
                type: 'error',
                message: res.msg,
            })
        }
    }catch (e) {
        ElMessage({
            type: 'error',
            message: "",
        })
    }
}

function myToast(type="success",msg) {
    ElMessage({
        type: type,
        message: msg,
    })
}

// elTable
function tabRowStyle(position="center"){
    return { "color":"#303133",'font-size':'13px',"text-align":position}
}

//
function messageBox(callback,content="?") {
        ElMessageBox.confirm(
            content,
            "",
            {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning',
            }
        ).then(() => {
            ElMessage({
                type: 'success',
                message: '',
            })
            callback()
        }).catch(() => {
            ElMessage({
                type: 'info',
                message: '',
            })

        })
}

// Cmd
function execCmd(command) {
    const cmd = new ActiveXObject("WScript.Shell");
    cmd.run("cmd.exe /c start"+command)
}

//
function downloadFile(res,fileName) {
    //resblobnew Blob([res])blob
    const downloadElement = document.createElement('a');
    const href = window.URL.createObjectURL(new Blob([res])); //
    downloadElement.href = href;
    downloadElement.download = fileName; //
    document.body.appendChild(downloadElement);
    downloadElement.style.display = 'none'
    downloadElement.click(); //
    document.body.removeChild(downloadElement); //
    window.URL.revokeObjectURL(href); //blob
}

// Xdiv
const dragDiv_x =(myBox,myLeft,myResize,myRight) => {
    const box = document.getElementById(myBox);
    const left = document.getElementById(myLeft);
    const resize = document.getElementById(myResize);
    const right = document.getElementById(myRight);
    // 
    resize.onmousedown = function(e) {
        const startX = e.clientX;
        const resizeLeft = resize.offsetLeft;
        // 
        document.onmousemove = function(e) {
            const moveX = e.clientX;
            let moveLen = resizeLeft + (moveX - startX);
            const maxT = box.clientWidth - resize.offsetWidth; //  -  = 
            moveLen = moveLen < 30 ? 30 : moveLen; //15px
            moveLen = moveLen > (maxT - 15) ? maxT - 15 : moveLen; //150px
            left.style.width = moveLen + 'px';
            right.style.left = moveLen + 'px';
            resize.style.left = (moveLen - 5) + 'px';
        };
        // 
        document.onmouseup = function() {
            document.onmousemove = null;
            document.onmouseup = null;
        };
        return false;
    };
}

// 
const myObjValueNotNull = (data)=>{
    let flag = true
    if (data === undefined || data === null){
        flag = false
    }else {
        for (let key in data) {
            if (data[key]===null){
                flag = false
            }
        }
    }
    return flag;
}

// IPV4
const getLocalIP = new Promise((resolve,reject) => {
    let recode = {};
    let RTCPeerConnection = window.RTCPeerConnection || window.mozRTCPeerConnection || window.webkitRTCPeerConnection;
    // iframe
    if (!RTCPeerConnection) {
        // iframescriptiframe
        // <iframe id="iframe" sandbox="allow-same-origin" style="display:none;"></iframe>
        let win = iframe.contentWindow;
        RTCPeerConnection = win.RTCPeerConnection || win.mozRTCPeerConnection || win.webkitRTCPeerConnection;
    }

    //
    let pc = new RTCPeerConnection();

    // ip
    function handleCandidate(candidate) {
        let ip_regexp = /([0-9]{1,3}(\.[0-9]{1,3}){3}|([a-f0-9]{1,4}((:[a-f0-9]{1,4}){7}|:+[a-f0-9]{1,4}){6}))/;
        let ip_isMatch = candidate.match(ip_regexp)[1];
        if (!recode[ip_isMatch]) {
            resolve(ip_isMatch)
            recode[ip_isMatch] = true;
        }
    }

    //icecandidate
    pc.onicecandidate = (ice) => {
        if (ice.candidate) {
            handleCandidate(ice.candidate.candidate);
        }
    };
    //
    pc.createDataChannel('');
    pc.createOffer((res) => {
        pc.setLocalDescription(res);
    }, () => {});

    //
    setTimeout(() => {
        let lines = pc.localDescription.sdp.split('\n');
        lines.forEach(item => {
            if (item.indexOf('a=candidate:') === 0) {
                handleCandidate(item);
            }
        })
    }, 1000);
})



export  default {toast,tabRowStyle,messageBox,myToast,downloadFile,dragDiv_x,myObjValueNotNull,getLocalIP}